.chat{
    display: flex;
   flex-direction: column;
}
.heading{
    margin: 5px;
    border-bottom: solid 1px;
    padding: 10px;
}
.main_chat{
    display: flex;
    justify-content: flex-start;
    border-bottom: solid 1px;
}
.avatar_user img{
    width: 50px;
    border-radius: 50%;
    margin: 5px;
    margin-left: 30px;
}
.name_message{
    display: flex;
    flex-direction: column;
    padding-left: 10px;
}
.name_user{
    padding-bottom: 10px;
}
.btn_post_reply{
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    margin-right: 20px;
}
.write_message textarea{
    display: block;
    width: 100%;
    min-height: 132px;
}
